@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
.App {
  box-sizing: border-box;
  border: 1px solid red;
  display: grid;
  grid-template-columns: 0.16fr 1fr;
  width: 100%;
  height: 100vh;
  background-color: rgba(37, 37, 219, 0.815);
}

.top {
  box-sizing: border-box;
  height: 80px;
  grid-column: span 2;
  
   
}

.main {margin: 1rem 0;
  box-sizing: border-box;
  height: calc(100vh - 85px);
 
  display: flex;
  gap: 1px;
  /* align-items: center; */
  justify-content: center;
}

.left {
  margin: 1rem 0;
  height: calc(100vh - 85px);
  box-sizing: border-box;
  padding: 2rem;
  display: flex;
  flex-direction: column;
}

button {
  padding: 1rem;
  margin: 1rem 2rem;
  color: black;
  background-color:rgb(71, 100, 194);
  font-family: 'Ubuntu', sans-serif;
  font-size: larger;
  font-weight: bolder;
  border-radius:5px ;
  box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.3);
  transition: all 0.4s ease-in;
}

button:hover {
  box-shadow: -10px -10px 5px rgba(0, 0, 0, 0.3);
}
